.ltn__no-gutter > [class*=col-] { /* No padding only for child columns */
  padding-right: 0;
  padding-left: 0;
}

.ltn__no-gutter-all [class*=col-] { /* No padding for every columns */
  padding-right: 0;
  padding-left: 0;
}

@media (min-width: 992px) { /* Modify this based on column def */
  .ltn__custom-gutter {
    margin-left: 0px;
    margin-right: 0px;
  }
  .ltn__custom-gutter > [class*=col-] {
    padding-right: 0px;
    padding-left: 0px;
  }
  .ltn__custom-gutter-all .row {
    margin-left: -15px;
    margin-right: -15px;
  }
  .ltn__custom-gutter-all .row [class*=col-] {
    padding-right: 15px;
    padding-left: 15px;
  }
}
@media (max-width: 991px) { /* Modify this based on column def */
  .ltn__custom-gutter {
    margin-left: -15px;
    margin-right: -15px;
  }
  .ltn__custom-gutter > [class*=col-] {
    padding-right: 15px;
    padding-left: 15px;
  }
  .ltn__custom-gutter-all {
    margin-left: -15px;
    margin-right: -15px;
  }
  .ltn__custom-gutter-all [class*=col-] {
    padding-right: 15px;
    padding-left: 15px;
  }
}
@media (max-width: 767px) { /* Modify this based on column def */
  .ltn__product-gutter .row {
    margin-left: -8px;
    margin-right: -8px;
  }
  .ltn__product-gutter [class*=col-] {
    padding-right: 8px;
    padding-left: 8px;
  }
}
/* ----------------------------------------------------
    Normalize    0B2C3D
---------------------------------------------------- */
:root {
  --ltn__primary-color: #000000;
  --ltn__primary-color-2: #041113; /* Darken */
  --ltn__primary-color-3: #133236; /* Lighten */
  --ltn__secondary-color: #2a52cb;
  --ltn__secondary-color-2: #0e3ed1; /* Darken */
  --ltn__secondary-color-3: #657dc3; /* Lighten */
  --ltn__heading-color: #000000;
  --ltn__paragraph-color: #0a0a0a;
  --ltn__btnwp-color: #25D366;
  --ltn__heading-font: "Satoshi", sans-serif;
  --ltn__body-font: "HostGrotesk Regular", sans-serif;
  /* Footer ACD2D8 8CB2B2  */
  --ltn__color-1: #8cb2b2;
  --ltn__color-2: #ACD2D8;
  --ltn__color-3: #A3BCC0;
  --ltn__color-4: #84A2A6;
  --ltn__color-5: #5C727D;
  --ltn__color-6: #F5DFDC;
  /* Gradient Colors */
  --gradient-color-1: linear-gradient(90deg, rgba(242,139,194,1) 0%, rgba(216,177,242,1) 50%);
  --gradient-color-2: linear-gradient(to top, rgba(7,28,31,0) 0%, rgba(7,28,31,1) 90%);
  --gradient-color-3: linear-gradient(to bottom, rgba(7,28,31,0) 0%, rgba(7,28,31,1) 90%);
  --gradient-color-4: linear-gradient(to top, rgba(242,246,247,0) 0%, rgba(242,246,247,1) 90%);
  --gradient-color-5: linear-gradient(to bottom, rgba(0,0,0,0) 0%, rgba(0,0,0,0.8) 70%);
  /* Background Colors ( 1, 2, 3, 4, 5 )  */
  --section-bg-1: #F2F6F7; /* White */
  --section-bg-2: #2a52cb; /* Blue */
  --section-bg-5: #FFF2F0; /* White */
  --section-bg-6: #000000; /* Black */
  --section-bg-7: #282b38; /* Black */
  /* Border Colors */
  --border-color-1: #e5eaee; /* White */
  --border-color-2: #1e2021; /* Black */
  --border-color-3: #5C617F; /* Black */
  --border-color-4: #2a52cb; /* Blue */
  --border-color-5: #657dc3; /* Blue */
  --border-color-6: #103034; /* Black */
  --border-color-7: #d1dae0; /* White */
  --border-color-8: #f6f6f6; /* White */
  --border-color-9: #e4ecf2; /* White */
  --border-color-10: #ebeeee; /* White */
  --border-color-11: #ededed; /* White */
  --border-color-12: #e1e6ff; /* White */
  --border-color-13: #f1f8ff; /* White */
  /* Box Shadow Colors */
  --ltn__box-shadow-1: 0 16px 32px 0 rgba(7, 28, 31, 0.1);
  --ltn__box-shadow-2: 0 0 4px rgba(0, 0, 0, 0.1); /* like border */
  --ltn__box-shadow-3: 0 1px 6px 0 rgba(32, 33, 36, .28); /* like border GGL */
  --ltn__box-shadow-4: 0 5px 20px 0 rgba(23, 44, 82, 0.1);
  --ltn__box-shadow-5: 0 8px 16px 0 rgba(93, 93, 93, 0.1);
  /* --ltn__box-shadow-6: 0 8px 16px 0 rgba(229, 62, 41, 0.70); */
  --ltn__box-shadow-6: 0 0 25px 2px rgba(93, 93, 93, 0.2);
  --ltn__box-shadow-7: 0 0 10px 0px rgba(93, 93, 93, 0.2);
  /* Common Colors */
  --black: #000000;
  --black-2: #22355B;
  --white: #fff;
  --white-2: #F2F6F7;
  --white-3: #e8edee;
  --white-4: #e6ecf0;
  --white-5: #f0f4f7;
  --white-6: #f1f1f1;
  --white-7: #F7F7F7;
  --white-8: #FAFAFA;
  --white-9: #F2F7FA;
  --white-10: #EDEDED;
  --red: #2a52cb;
  --red-2: #0e3ed1;
  --red-3: #657dc3;
  --silver: #C0C0C0;
  --gray: #808080;
  --maroon: #800000;
  --yellow: #FFFF00;
  --olive: #808000;
  --lime: #00FF00;
  --green: #25D366;
  --green-2: #008000;
  --aqua: #00FFFF;
  --teal: #008080;
  --blue: #0000FF;
  --navy: #000080;
  --fuchsia: #B13BFF;
  --purple: #800080;
  --pink: #FFC0CB;
  --nude: #ebc8b2;
  --orange: #ffa500;
  --ratings: #FFB800;
  /* social media colors */
  --facebook: #365493;
  --twitter: #3CF;
  --linkedin: #0077B5;
  --pinterest: #c8232c;
  --dribbble: #ea4c89;
  --behance: #131418;
  --google-plus: #dd4b39;
  --instagram: #e4405f;
  --vk: #3b5998;
  --wechat: #25D366;
  --youtube: #CB2027;
  --email: #F89A1E;
}

/* -------------------------------------
    Typography
------------------------------------- */
html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  font-family: var(--ltn__body-font);
}

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;
  -moz-osx-font-smoothing: grayscale;
  /* Firefox */
  -webkit-font-smoothing: antialiased;
  /* WebKit  */
}

body {
  color: var(--ltn__paragraph-color);
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  font-family: var(--ltn__body-font);
  line-height: 1.8;
  margin: 0 auto;
}

h1, .h1 {
  font-size: 36px;
  line-height: 1.2;
}

h2, .h2 {
  font-size: 30px;
}

h3, .h3 {
  font-size: 24px;
}

h4, .h4 {
  font-size: 20px;
}

h5, .h5 {
  font-size: 18px;
}

h6, .h6 {
  font-size: 16px;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  color: var(--ltn__heading-color);
  clear: both;
  font-family: var(--ltn__heading-font);
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 15px;
}

p {
  color: var(--ltn__paragraph-color);
  margin-bottom: 1.5em;
  -webkit-hyphens: auto;
  -moz-hyphens: auto;
  -ms-hyphens: auto;
  hyphens: auto;
}

a {
  color: inherit;
  text-decoration: none;
  color: var(--ltn__paragraph-color);
  transition: all 0.3s ease 0s;
}

a,
a:hover,
a:focus,
a:active {
  text-decoration: none;
  outline: none;
  color: inherit;
}

a:hover {
  color: var(--ltn__secondary-color);
}

button,
input,
optgroup,
select,
textarea {
  font-family: var(--ltn__body-font);
}

pre,
code,
kbd,
tt,
var,
samp {
  font-family: var(--ltn__body-font);
}

pre {
  word-break: break-word;
}

a i {
  padding: 0 2px;
}

img {
  max-width: 100%;
}

ul li,
ol li {
  margin-top: 1rem;
}

