  letter-spacing: 3px;
  font-size: 16px;
}

textarea {
  resize: vertical;
  padding: 15px 0;
  min-height: 150px;
}

button {
  outline: none;
  border: none;
  cursor: pointer;
}

button:focus {
  outline: none;
}

.form-input-box {
  position: relative;
}

.form-input-box input[type=text] {
  width: 100%;
  height: 50px;
  padding: 0 70px 0 15px;
  border-radius: 0 15px 0px 0px;
  border: 1px solid var(--ltn__primary-color);
  margin-bottom: 0;
}

.form-input-box button[type=submit] {
