  }
  .owl-arrow-1 .owl-nav > div.owl-next {
    right: 0px;
    left: auto;
  }
}
/* ----------------------------------------------------
    Slick Slider Dots, Arrow
---------------------------------------------------- */
/* Slick dots */
.slick-dots {
  margin: 0 0 30px;
  padding: 0;
  display: block;
  text-align: center;
  line-height: 1;
}
.slick-dots li {
  display: inline-block;
  list-style: none;
  display: inline-block;
  font-size: 0;
  height: 10px;
  width: 10px;
  border-radius: 100%;
  margin-right: 10px;
  background-color: var(--silver);
  cursor: pointer;
  margin-top: 0;
  transition: all 0.3s ease 0s;
}
.slick-dots li button {
  display: none;
}
.slick-dots li:hover, .slick-dots li.slick-active {
  background-color: var(--ltn__secondary-color);
  height: 15px;
  width: 15px;
  margin-bottom: -2px;
}

/* slick-arrow */
.slick-arrow {
  cursor: pointer;
  z-index: 9;
}

/* slick-arrow-1 */
.slick-arrow-1 .slick-arrow {
  background-color: var(--white);
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 15px;
  right: auto;
  transform: translateY(-50%);
  width: 50px;
  height: 50px;
  line-height: 48px;
  display: block;
  border: 1px solid var(--white-4);
  border-radius: 100%;
  text-align: center;
  font-size: 20px;
  color: var(--ltn__primary-color) !important;
  z-index: 1;
  opacity: 0;
  visibility: hidden;
}
.slick-arrow-1 .slick-arrow:hover {
  background-color: var(--ltn__secondary-color);
  border-color: var(--ltn__secondary-color);
  color: var(--white) !important;
}
.slick-arrow-1 .slick-next {
  right: 15px;
  left: auto;
}
.slick-arrow-1:hover .slick-arrow {
  opacity: 1;
  visibility: visible;
}

.slick-arrow-1-inner.slick-arrow-1:hover .slick-arrow {
  left: 50px;
  right: auto;
}
.slick-arrow-1-inner.slick-arrow-1:hover .slick-next {
  right: 50px;
  left: auto;
}

@media (min-width: 1350px) {
  .slick-arrow-1:hover .slick-arrow {
    left: -50px;
    right: auto;
  }
  .slick-arrow-1:hover .slick-next {
    right: -50px;
    left: auto;
  }
}
/* slick-arrow-2 */
.slick-arrow-2 .slick-arrow {
  color: var(--ltn__primary-color) !important;
  cursor: pointer;
  position: absolute;
  bottom: -30px;
  height: 30px;
  width: 30px;
  line-height: 28px;
  display: block;
  left: 15px;
  border: 1px solid var(--border-color-1);
  text-align: center;
}
.slick-arrow-2 .slick-arrow:hover {
  background-color: var(--ltn__secondary-color);
  border-color: var(--ltn__secondary-color);
  color: var(--white) !important;
}
.slick-arrow-2 .slick-next {
  left: 60px;
}

/* slick-arrow-3 */
.slick-arrow-3 .slick-arrow {
  cursor: pointer;
  position: absolute;
  top: -120px;
  height: 60px;
  width: 60px;
  line-height: 58px;
  display: block;
  left: auto;
  right: 100px;
  border: 2px solid;
  text-align: center;
  box-shadow: var(--ltn__box-shadow-2);
}
.slick-arrow-3 .slick-arrow:hover {
  background-color: var(--ltn__secondary-color);
  border-color: var(--border-color-4);
  color: var(--white) !important;
}
.slick-arrow-3 .slick-next {
  right: 15px;
  background-color: var(--white);
  border-color: var(--white);
  color: var(--ltn__primary-color) !important;
}
.slick-arrow-3 .slick-prev {
  background-color: var(--ltn__secondary-color);
  border-color: var(--border-color-4);
  color: var(--white) !important;
}

/* slick-arrow-4 */
.slick-arrow-4 {
  margin: 0;
}
.slick-arrow-4 .slick-arrow {
  position: absolute;
  bottom: -50px;
  left: 0;
  right: 0;
  text-align: center;
  height: 50px;
  width: 50px;
  line-height: 50px;
  margin: 0 auto;
  border: 1px solid;
  border-radius: 100%;
}
.slick-arrow-4 .slick-arrow:hover {
  background-color: var(--ltn__secondary-color);
  border-color: var(--border-color-4);
  color: var(--white) !important;
}
.slick-arrow-4 .slick-next {
  right: -60px;
}
.slick-arrow-4 .slick-prev {
  left: -60px;
}

@media (max-width: 767px) {
  .slick-dots {
    margin: 0 0 15px;
  }
}
