/* ----------------------------------------------------
    Product Area
---------------------------------------------------- */
.ltn__product-item {
  position: relative;
  margin-bottom: 50px;
}

.product-img {
  position: relative;
  overflow: hidden;
}

.product-img img {
  position: relative;
  transition: all 3.5s ease 0s;
}

.product-img:before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--ltn__primary-color);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease 0s;
}

.ltn__product-item:hover .product-img:before {
  opacity: 0.4;
  visibility: visible;
}

.ltn__product-item:hover .product-img::before {
  opacity: 0.4;
  visibility: visible;
}

.product-title {
  font-size: 18px;
  margin-bottom: 5px;
}

.product-ratting {
  margin-bottom: 5px;
}

.product-ratting ul {
  margin: 0;
  padding: 0;
}

.product-ratting li {
  display: inline-block;
  margin: 0 -2px;
  color: var(--ratings);
  font-size: 12px;
}

.product-hover-action {
  position: absolute;
  top: 60%;
  left: 0;
  right: 0;
  text-align: center;
  transform: translateY(-50%);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease 0s;
}

.ltn__product-item:hover .product-hover-action {
  top: 50%;
  opacity: 1;
  visibility: visible;
}

.product-hover-action ul {
  margin: 0;
  padding: 0;
  display: inline-block;
  box-shadow: var(--ltn__box-shadow-3);
}

.product-hover-action li {
  height: 50px;
  width: 50px;
  line-height: 50px;
  font-size: 14px;
  text-align: center;
  float: left;
  margin-right: 0px;
  margin-top: 0;
  list-style: none;
  border-right: 1px solid var(--border-color-7);
}

.product-hover-action li:last-child {
  margin-right: 0;
  border-right: 0;
}

.product-hover-action li a {
  display: block;
  background-color: var(--white);
  font-weight: 700;
}

.product-hover-action li:hover a {
  background-color: var(--ltn__secondary-color);
  color: var(--white);
}

.product-badge {
  position: absolute;
  top: 15px;
  right: 18px;
}

.product-badge ul {
  margin: 0;
  padding: 0;
}

.product-badge li {
  list-style: none;
  display: inline-block;
  font-size: 13px;
  font-weight: 600;
  background-color: var(--ltn__secondary-color);
  color: var(--white);
  padding: 3px 15px 0px;
  text-transform: uppercase;
  line-height: 25px;
}
.product-badge li:first-child {
  margin-top: 0;
}
.product-badge li::before {
  position: absolute;
  content: "";
  bottom: -8px;
  left: 0;
  border-left: 8px solid var(--ltn__secondary-color);
  width: 0;
  height: 0;
  border-bottom: 8px solid transparent;
  display: none;
}
.product-badge li .soldout-badge {
  background-color: var(--red-2);
}

.product-info {
  padding: 20px 0 0;
}

.product-price {
  color: var(--ltn__heading-color);
  font-weight: 600;
}

.product-price del {
  opacity: 0.6;
  margin-left: 5px;
  font-size: 80%;
}

.ltn__product-item .product-price {
  margin-bottom: 12px;
}

.product-action ul {
